.border {
  border: $border;
}
.borderTopDarkblue {
  border-top: solid 3px $deep-blue;
}
.border-bottom {
  border-bottom: 1px solid #dee2e6;
}

.d-block {
  display: block !important;
}
.d-none {
  display: none !important;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.block-center {
  margin-left: auto;
  margin-right: auto;
}

.section-title {
  border-top: 3px solid $deep-blue;
  border-bottom: 1px solid $soft-bluish-gray;
  line-height: 20px;
  font-size: 14px;
  text-transform: uppercase;
  padding: 12px 18px;
  color: $bright-gray;
  font-weight: 400;
}

.heading-brand {
  //position: relative; z-index: 3; top: 45vh;
  &::after {
    content: url(../img/dsa-brand-line.jpg);
    display: block;
  }
}

.page-title{
  border-top: 3px solid $deep-blue;
  padding-top: 10px;
}
.text-white {
  color: $white !important;
  a {
    color: $white;
  }
}
.transparent-bg {
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
}
.center-block {
  margin: 0 auto;
  display: block;
}
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.shadow {
  @include box-shadow(0 0px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19));
}
.img-circle {
  @include border-radius(50%);
  border: $border-default;
}
.zoom-on-hover img {
  @include transition(transform 0.5s ease);
  width: 100%;
}
.zoom-on-hover:hover img {
  @include transform(scale(1.2));
}
.blue-hover {
  &:hover {
    background-color: $dark-light-blue;
    color: $white;
    a,
    h4,
    span {
      color: $white;
    }
  }
}
.dark-overlay {
  background: $deep-blue;
  &[data-reduce-opacity] {
    opacity: 0.8;
  }
}

.down-arrow {
  border: solid #9599a0;
  position: relative;
  margin-left: 5px;
  top: -3px;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  user-select: none;
  @include transform(rotate(45deg));
}
.float-left {
  float: left;
}
.float-right {
  float: right;
}
.float-none {
  float: none;
}
.required-field {
  color: red;
}

.text-lowercase {
  text-transform: lowercase !important;
}
.text-uppercase {
  text-transform: uppercase !important;
}
.text-capitalize {
  text-transform: capitalize !important;
}
.text-capitalize-none {
  text-transform: none !important;
}
.overlay-black::after {
  background-color: $white;
  content: "";
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0.8;
}

.clearfix {
  @extend %clearfix;
}
.text-muted {
  color:$gray-700;
}
.img-fluid {
  max-width: 100%;
  height: auto;
}
.align-middle {
  vertical-align: middle!important;
}
.img-bg{
background-position: center;
background-repeat: no-repeat;
background-attachment: scroll;
background-size: cover;
min-height: 500px;
}
