.home {
  header.site-header {
    #main-nav{
      background:rgba($bluedark,0.8);
    }
  }
  
  .big-banner {
    .item{
      position: relative;
      background-color: $bluedark;
      height: 100vh;
      width: 100%;
      max-height: 1000px;

      .item-image {
        z-index:0;
        height:100%;
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          z-index: 0;
          background: linear-gradient(0deg, rgba(13, 35, 56, 20) 20%, rgba(13, 35, 56, 0.06774047509628855) 65%);
        }
        img {
          background-size:contain;
          width: 100%;
          height:100%;
        }
      }

      .news {
        position: absolute;
        bottom: 10%;
        left: 50%;
        @include transform(translateX(-50%));

        h1{
          text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.46);
          margin-bottom:20px;
        }
        .grid.align-end {
          .col-grid {
            position: relative;
            padding: 0;
            background: $deep-blue;
            img {
              ///filter: brightness(50%);
              opacity: 0.4;

              
            }

            .news-details {
              position: absolute;
              left: 50%;
              @include transform(translateX(-50%));
              width: 90%;
              top:40%;//add top postion due to justify-center not workin in ie
              a{color:white;}
              .btn-link{ 
                padding-left: 0; 
                padding-right: 0;
              }
            }
          }

        }
      }
    }
    
  }
  //end of homepage big banner css

  section.about-us {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.25) 9%, rgba(0, 0, 0, 0.25) 9%, rgba(0, 0, 0, 0.25) 9%),
      url(../img/who-we-are.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;
    min-height: 500px;
    .transparent-bg {
      min-height: 370px;
      color: $white;
      position: relative;
      @include transition(0.5s background-color ease-in-out);
      h2 {
        color: $white;
      }
      a {
        color: $white;
      }
      &:hover {
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8) 9%, rgba(0, 0, 0, 0.5) 9%, rgba(0, 0, 0, 0.8) 9%);
        cursor: pointer;
        .icon-3x{color:$blue;}
      }
      .center-block {
        margin-top: 0px;
        margin-bottom: 20px;
      }
    }
  }
  .card.events {
    .card-body {
      height: 450px;
      overflow: auto;
    }
  }

  .card.asiapedia {
    .card-body {
      form {
        margin: 0 auto;
        display: flex;
      }
      select {
        border: none;
        background-color: #e5e5e5;
        color: $gray-700;
        padding: 10px 20px;
        width: 100%;
        font-size: $font-size-sm;
      }
    }
  }
}
//Mobile
@media (max-width: $reflex-md) {
  .big-banner {
    .item {
      //150vh because mobile screen when view on potrait mode is VERY LONG and we must accomodate 3 news item verticaly stacked together.
      //if any better alternatives, feel free to make change here
      height: 150vh !important;

      .item-image{
        img{zoom:4;width:auto !important;}
      }

      .news {
        bottom: auto;
        top: 25%; //stick the news towards the top rather than bottom on mobile to reduce the odd gap between header and news on mobile's portrait mode
        .heading-brand {
          font-size:1.3em;
          margin-top: 0 !important;
        }
        .col-grid {
          max-height: 150px;
          overflow: hidden;
          margin-bottom: 5px;
          .news-details{
            top: 20%;//add top postion due to justify-center not workin in ie
          }
        }
      }
    }
  }
  .home {
    section.about-us {
      .transparent-bg {
        min-height: auto;
        .center-block{ display: contents;}
      }
    }
  }
}
